}


.function-name-link:hover {
  color: var(--color-primary-hover);
}

.extension-name {
  font-weight: var(--font-weight-semibold);
  color: var(--color-text);
  font-size: var(--font-size-lg);
  margin: 0;
}

.extension-body {
  padding: var(--space-16);
}

.database-counter {
  font-size: var(--font-size-sm);
  color: var(--color-text-secondary);
  background-color: var(--color-secondary);
  padding: var(--space-4) var(--space-8);
  border-radius: var(--radius-full);
  transition: all var(--duration-normal) var(--ease-standard);
}

/* Theme Toggle */
.theme-toggle {
  background: var(--color-secondary);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-full);
  padding: var(--space-8);
  cursor: pointer;
  transition: all var(--duration-normal) var(--ease-standard);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
}

.theme-toggle:hover {
  background: var(--color-secondary-hover);
  transform: scale(1.05);
}

.theme-toggle:focus {
  outline: var(--focus-outline);
}

.theme-icon {
  font-size: var(--font-size-lg);
  transition: transform var(--duration-normal) var(--ease-standard);
}

.theme-toggle:hover .theme-icon {
  transform: rotate(20deg);
}

/* Main Layout */
.main-layout {
  display: flex;
  min-height: calc(100vh - 80px);
  transition: all var(--duration-normal) var(--ease-standard);
}

.sidebar {
  width: 320px;
  background-color: var(--color-surface);
  border-right: 1px solid var(--color-border);
  display: flex;
  flex-direction: column;
  transition: all var(--duration-normal) var(--ease-standard);
}

.sidebar-header {
  padding: var(--space-20);
  border-bottom: 1px solid var(--color-border);
}

.sidebar-title {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-semibold);
  color: var(--color-text);
  margin-bottom: var(--space-16);
  transition: color var(--duration-normal) var(--ease-standard);
}

.add-database-btn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-8);
  padding: var(--space-12) var(--space-16);
  background: var(--color-primary);
  color: var(--color-btn-primary-text);
  border: none;
  border-radius: var(--radius-base);
  cursor: pointer;
  font-weight: var(--font-weight-medium);
  transition: all var(--duration-normal) var(--ease-standard);
}

.add-database-btn:hover {
  background: var(--color-primary-hover);
  transform: translateY(-1px);
}

.sidebar-content {
  flex: 1;
  overflow-y: auto;
  padding: var(--space-16);
}

/* Database List */
.database-list {
  display: flex;
  flex-direction: column;
  gap: var(--space-12);
}

.database-item {
  background: var(--color-background);
  border: 2px solid var(--color-border);
  border-radius: var(--radius-lg);
  padding: var(--space-16);
  cursor: pointer;
  transition: all var(--duration-normal) var(--ease-standard);
  position: relative;
}

.database-item:hover {
  border-color: var(--color-primary);
  box-shadow: var(--shadow-md);
}

.database-item.active {
  border-color: var(--color-primary);
  background: var(--color-bg-1);
  box-shadow: var(--shadow-md);
}

.database-item-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: var(--space-8);
}

.database-name {
  font-weight: var(--font-weight-semibold);
  color: var(--color-text);
  transition: color var(--duration-normal) var(--ease-standard);
}

.database-status {
  display: flex;
  align-items: center;
  gap: var(--space-6);
  font-size: var(--font-size-sm);
}

.status-indicator {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  animation: pulse 2s infinite;
}

.status-indicator.new {
  background-color: var(--color-info);
  animation: none;
}

.status-indicator.analyzing {
  background-color: var(--color-warning);
}

.status-indicator.done {
  background-color: var(--color-success);
  animation: none;
}

.status-indicator.error {
  background-color: var(--color-error);
  animation: none;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

.database-details {
  font-size: var(--font-size-sm);
  color: var(--color-text-secondary);
  margin-bottom: var(--space-8);
  transition: color var(--duration-normal) var(--ease-standard);
}

.database-progress {
  width: 100%;
  height: 4px;
  background-color: var(--color-secondary);
  border-radius: var(--radius-sm);
  overflow: hidden;
  margin-top: var(--space-8);
}

.progress-fill {
  height: 100%;
  background-color: var(--color-primary);
  transition: width var(--duration-normal) var(--ease-standard);
}

.database-actions {
  position: absolute;
  right: 5px;
  transition: opacity var(--duration-normal) var(--ease-standard);
  top: 0px;
}

.database-item:hover .database-actions {
  opacity: 1;
}

.remove-btn {
  background: var(--color-error);
  color: white;
  border: none;
  border-radius: var(--radius-sm);
  padding: var(--space-2) var(--space-4);
  font-size: var(--font-size-xs);
  cursor: pointer;
  transition: all var(--duration-normal) var(--ease-standard);
}

.remove-btn:hover {
  background: var(--color-red-400);
  transform: scale(1.05);
}

/* Main Content */
.main-content {
  flex: 1;
  padding: var(--space-32);
  overflow-y: auto;
  transition: all var(--duration-normal) var(--ease-standard);
}

.empty-state {
  text-align: center;
  padding: var(--space-32);
  color: var(--color-text-secondary);
  max-width: 500px;
  margin: 0 auto;
  transition: color var(--duration-normal) var(--ease-standard);
}

.empty-state h2 {
  font-size: var(--font-size-2xl);
  margin-bottom: var(--space-16);
  color: var(--color-text);
  transition: color var(--duration-normal) var(--ease-standard);
}

.empty-state-icon {
  font-size: 64px;
  margin-bottom: var(--space-24);
  opacity: 0.6;
}

/* Database Form Modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  transition: all var(--duration-normal) var(--ease-standard);
}

.modal.hidden {
  display: none;
}

.modal-content {
  background: var(--color-surface);
  border-radius: var(--radius-lg);
  padding: var(--space-32);
  width: 90%;
  max-width: 500px;
  box-shadow: var(--shadow-lg);
  transition: all var(--duration-normal) var(--ease-standard);
}

.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: var(--space-24);
  border-bottom: 1px solid var(--color-border);
  padding-bottom: var(--space-16);
}

.checkbox-group {
  display: flex;
  align-items: center;
  gap: var(--space-8);
  margin-bottom: var(--space-16);
}

.cron-section {
  margin-bottom: var(--space-24);
}

.cron-presets {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(1fr, 400px));
  gap: var(--space-8);
  margin-top: var(--space-12);
}

.checkbox-group input[type="checkbox"] {
  width: 18px;
  height: 18px;
}


.preset-btn {
  background: var(--color-background);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-base);
  padding: var(--space-8);
  text-align: left;
  cursor: pointer;
  transition: all var(--duration-fast) var(--ease-standard);
}

.preset-btn:hover {
  background: var(--color-secondary);
}

.preset-btn.selected {
  background: var(--color-primary);
  color: var(--color-btn-primary-text);
  border-color: var(--color-primary);
}

.cron-validation {
  margin-top: var(--space-8);
  font-size: var(--font-size-sm);
}

.cron-validation.valid {
  color: var(--color-success);
}

.cron-validation.invalid {
  color: var(--color-error);
}

.modal-footer {
  display: flex;
  gap: var(--space-12);
  justify-content: flex-end;
  border-top: 1px solid var(--color-border);
  padding-top: var(--space-16);
}

.modal-title {
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-bold);
  color: var(--color-text);
  margin: 0;
}

.close-btn {
  background: none;
  border: none;
  font-size: var(--font-size-xl);
  color: var(--color-text-secondary);
  cursor: pointer;
  transition: color var(--duration-normal) var(--ease-standard);
}

.close-btn:hover {
  color: var(--color-text);
}

.form-group {
  margin-bottom: var(--space-16);
}

.form-actions {
  display: flex;
  gap: var(--space-12);
  justify-content: flex-end;
  margin-top: var(--space-24);
}

